body {
	font-family: 'Noto Serif', serif;

	background: #acb6e5; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to left,
		#acb6e5,
		#86fde8
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to left,
		#acb6e5,
		#86fde8
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

button:disabled {
	cursor: not-allowed;
}

main {
	width: 300px;
}

footer {
	color: #f1e9e5;
}
/*end*/

/*start custom Clssess */
.main-title {
	color: #e63e6d;
}

.fit-content {
	width: fit-content;
}

.txt-color {
	color: #697b97;
}

.header-color {
	color: #415577;
}

.pointer-cursor {
	cursor: pointer;
}
/*end*/

/*start style music-palyer */
.music-palyer {
	background-color: #dee8ef;
	border-radius: 35px;
	box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
		rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}
/*end*/

/*start style music-palyer image */
.music-palyer__image {
	width: 180px;
	height: 180px;
	box-shadow: rgba(14, 30, 37, 0.12) 0px 3px 6px 0px,
		rgba(14, 30, 37, 0.32) 0px 5px 10px 0px;

	border: 8px solid #dee8ef;
}

.animate-rotate {
	/* opacity: 0.2; */
	animation-name: rotate;
	animation-duration: 10s;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
	animation-play-state: running;
}

.animate-rotate.pause {
	animation-play-state: paused;
}

@keyframes rotate {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
/*end*/

/*start style music-palyer progress time */
/* https://stackoverflow.com/questions/42290719/custom-styling-progress-bar-in-css */
progress {
	border-radius: 7px;
	width: 80%;
	height: 8px;
	/* box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2); */
	box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 2px 0px,
		rgba(14, 30, 37, 0.32) 0px 2px 4px 0px;
}
progress::-webkit-progress-bar {
	background-color: rgba(204, 204, 204, 0.4);
	border-radius: 7px;
}
progress::-webkit-progress-value {
	/* background-color: #2f5d62; */
	background-color: rgba(67, 87, 122, 0.7);
	border-radius: 7px;
	/* box-shadow: 1px 1px 1px 1px rgba(51, 71, 86, 0.8); */
}
/*end*/

/*start style music-palyer control icons  */
.control-icon {
	font-size: 20px;
	color: rgb(105, 123, 151);
	display: block;
	padding: 5px;
	border: 1px solid #eee;
	border-radius: 50%;
	box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px,
		rgba(14, 30, 37, 0.32) 0px 2px 4px 0px;

	transition: all 0.2s ease-in-out;
}

.control-icon:hover {
	opacity: 0.7;
}

.control-icon:active {
	transform: translateY(2px);
}

.music-palyer__controls__play {
	padding: 3px;
	font-size: 35px;
}
/*end*/

/*start style music-palyer music bars  */
